import React, { useState } from 'react';
import { Navbar, Container, Nav, NavDropdown, Jumbotron, Button, Form, FormControl } from 'react-bootstrap';
import './App.css';
import Data from './data.js';
import { Link, Route, Switch } from 'react-router-dom'
import Detail from './detail.js';
import axios from 'axios';

function App() {
  let [shoes, setShoes] = useState(Data);
  let [재고, set재고] = useState([10, 11, 12]);
  return (
    <div className="App">
      <Navbar bg="light" expand="lg">
        <Container>
          <Navbar.Brand href="#home">ShoeShop</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link> <Link to="/">Home</Link></Nav.Link>
              <Nav.Link> <Link to="/detail">Detail</Link></Nav.Link>
              <NavDropdown title="Dropdown" id="basic-nav-dropdown">
                <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
              </NavDropdown>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>

      <Route exact path="/">
        <Jumbotron className="background">
          <h1>20% Season Off</h1>
          <p>
            This is a simple hero unit, a simple jumbotron-style component for calling
            extra attention to featured content or information.
          </p>
          <p>
            <Button variant="primary">Learn more</Button>
          </p>
        </Jumbotron>
        <div className="container">
          <div className="row">
            {
              shoes.map((a, i) => {
                return <Card shoes={shoes[i]} i={i} />
              })
            }
          </div>
          <button className="btn btn-primary" onClick={()=>{

            axios.get('https://codingapple1.github.io/shop/data2.json')  //서버에게 GET요청
            .then((result)=>{
              setShoes([...shoes, ...result.data]);
            })
            .catch(()=>{
              console.log('fail');
            })
            ; 

          }}>더보기</button>
        </div>
      </Route>
      <Route path="/detail/:id">
            <Detail shoes={shoes} 재고={재고} 재고변경={set재고}/>
      </Route>
      <Route path="/:id"></Route>
    </div>
  );
}

function Card(props) {
  return (
    <div className="col-md-4">
      <img src={'https://codingapple1.github.io/shop/shoes' + (props.i + 1) + '.jpg'} width="100%" />
      <h4>{props.shoes.title}</h4>
      <p>{props.shoes.content}</p>
      <p>{props.shoes.price}</p>
    </div>
  )
}
export default App;
